.header__bacground {
    background-color: rgb(229, 228, 228);
}

.header {
    margin: 0 auto;
    max-width: 1280px;
    background-color: rgb(229, 228, 228);
    position: relative;
}

.header__image {
    width: 100%;
}

.header__title {
    position: absolute;
    top: 150px;
    left: 92px;
    font-size: 30px;
    animation: fadeIn 2s ease-in-out;
    opacity: 0;
    animation-fill-mode: forwards;
}

.header__name {
    margin: 0;
    font-family: "Oswald", sans-serif;
    color: #353535;
    font-size: 126px;
}

.header__date {
    margin: 0;
    font-family: "Oswald", sans-serif;
    color: #353535;
    font-size: 60px;
    position: relative;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        transform: translateY(-20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@media (max-width: 1000px) {
    .header__title {
        font-size: 20px;
    }
}

@media (max-width: 770px) {
    .header__name {
        font-size: 55px;
    }
    .header__date {
        font-size: 40px;
    }
    .header__title {
        top: 100px;
        left: 50px;
    }
}

@media (max-width: 450px) {
    .header__title {
        font-size: 15px;
    }
    .header__name {
        font-size: 39px;
    }
    .header__date {
        font-size: 26px;
    }
}

@media (max-width: 350px) {
    .header__title {
        font-size: 10px;
        top: 69px;
        left: 18px;
    }
}
